<template>
    <div>
        <div>
            <div>
                <select name="line"
                        id="line"
                        v-model="selected.line"
                        @change="selectLine()">
                    <option value="" disabled selected>Linha</option>
                    <option v-for="elem in options.line"
                            :value="elem.value"
                    >{{elem.text}}
                    </option>
                </select>
            </div>
            <div v-if="isLineSelected">
                <select name="station" id="station">
                    <option value="" disabled selected>Estação</option>
                    <option v-for="elem in options.stations[selected.line]"
                    value="">{{elem}}</option>
                </select>
            </div>
        </div>
    </div>

</template>

<script>
    export default {
        name: "RudimentaryView",
        data: function () {
            return {
                isLineSelected: false,
                options: {
                    line: [
                        {
                            value: 'blue',
                            text: 'Azul'
                        },
                        {
                            value: 'red',
                            text: 'Vermelha'
                        }
                    ],
                    stations: {
                        blue: [
                            'AltoDosMoinhos',
                            'Laranjeiras',
                            'SeteRios'
                        ],
                        red: [
                            'Oriente',
                            'CaboRuivo',
                            'Olivais'
                        ]
                    }
                },
                selected: {
                    line: '',
                    station: ''
                }
            }
        },
        methods: {
            consoler: function () {
                console.log(this.selected.line)
            },
            selectLine: function () {
                this.isLineSelected = true;

            }
        }
    }
</script>

